import React, { useState } from "react";
import { Flex } from "reflexbox/styled-components";
import styled, { css } from "styled-components";
import Gravatar from "react-gravatar";

import { useUser } from "../../../UserData";
import CurrentBalance from "./CurrentBalance";
import PayDialog from "./PayDialog";
import BillingHistory from "./BillingHistory";

import Bar from "../../elements/Bar";

const Page = styled(Flex).attrs({
  flexDirection: "row"
})`
  width: 100%;
  padding: 50px 20px;
  box-sizing: border-box;
`;

const Card = styled(Flex).attrs({
  alignItems: "center",
  flexDirection: "column",
  justifyContent: "space-between"
})`
  background-color: ${props => props.theme.palette.secondary};
  color: ${props => props.theme.palette.inverse};
  box-shadow: ${props => props.theme.dimension.boxShadow + ' ' + props.theme.palette.boxShadow};
  text-align: center;
`;

const Avatar = styled(Gravatar)`
  width: auto;
  height: auto;
  max-width: 100%;
  min-width: 33%;
  border-radius:50%;
  border: 5px solid ${props => props.theme.palette.primary};
`;

let [positive, negative] = ["positive", "negative"];
const balanceColor = css`
  color: ${props =>
  props.theme.palette[parseInt(props.children) > 0 ? positive : negative]};
`;

const Balance = styled.big`
  font-size: 6vw;
  font-weight: bold;
  width: 100%;
  ${balanceColor}
`;

const PurchaseButton = styled.button`
  padding: 1vh 0.5vw;
  border-radius: 10px;
  font-size: 2.5vw;
  align-self: stretch;
  font-weight: bold;
  background-color: ${props => props.theme.palette.primary};
  color: ${props => props.theme.palette.primaryText};
  text-decoration: none;
  border: none;
`;

const PrimaryButton = styled.button`
  cursor: pointer;
  padding: 5px;
  border-radius: 3px;
  font-weight: bold;
  background-color: ${props => props.theme.palette.primary};
  color: ${props => props.theme.palette.primaryText};
  border: none;
  height: fit-content;
`;

const TransactionTopBar = styled(Bar).attrs({
  justifyContent: "space-between",
  p: "10px"
})`
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.59);
`;

export default function UserPage() {
  const [showShareDialog, setShowShareDialog] = useState(false);
  const [showBillingDialog, setShowBillingDialog] = useState(false);
  const { user, bill, logout } = useUser();
  if (!user) {
    return null;
  } else {
    return (
      <Page>
        <PayDialog
          open={showBillingDialog}
          onClose={() => setShowBillingDialog(false)}
          onBill={() => bill(-200, "Top up from credit")}
        />
        <Card
          width="33%"
          height="75vh"
          mr="20px"
          px="10px"
          py="20px"
        >
          <Avatar email={user.email}/>
          <h1 className="user-card-username">{user.username}</h1>
          <h3 className="user-card-email">{user.email}</h3>
          <Balance>{user.balance}$</Balance>
          <PurchaseButton
            onClick={() => {
              setShowBillingDialog(true);
            }}
          >
            Buy more tokens!
          </PurchaseButton>
        </Card>
        <Card height="75vh" width="66%">
          <TransactionTopBar>
            <PrimaryButton onClick={() => setShowShareDialog(true)}>
              Share current balance
            </PrimaryButton>
            <PrimaryButton onClick={logout}>Logout</PrimaryButton>
          </TransactionTopBar>
          <CurrentBalance
            value={`My current balance at Clover is: ${user.balance}`}
            open={showShareDialog}
            onClose={() => setShowShareDialog(false)}
          />
          <BillingHistory/>
        </Card>
      </Page>
    );
  }
}