import React from "react";
import styled from "styled-components";
import { Box, Flex } from "reflexbox/styled-components";
import Main from "./Main/Main";
import MainTopBar from "./Main/MainTopBar";

const AppContainer = styled(Flex).attrs({
  height: "100%",
  width: "100%",
  flexDirection: "column"
})`
  background-color: ${props => props.theme.palette.dark};
  color: ${props => props.theme.palette.text};

  ${props =>
  Object.keys(props.theme.palette)
    .map(color => `--${color}: ${props.theme.palette[color]};`)
    .join("\n")}
  ${props =>
  Object.keys(props.theme.dimension)
    .map(size => `--${size}: ${props.theme.dimension[size]};`)
    .join("\n")}

`;

function App() {
  return (
    <AppContainer>
      <MainTopBar />
      <Box height="calc(100% - 50px)">
        <Main/>
      </Box>
    </AppContainer>
  );
}

export default App;
