import React from "react";
import styled from "styled-components";
import { Link } from '@reach/router';
import { Box, Flex } from "reflexbox/styled-components";

const Card = styled(Flex).attrs({
  px: "10px",
  py: "20px",
  alignItems: "center",
  flexDirection: "column"
})`
  background-color: ${props => props.theme.palette.inverse};
  color: ${props => props.theme.palette.inverseText};
  box-shadow: ${props => props.theme.dimension.boxShadow + ' ' + props.theme.palette.boxShadow};
  text-align: center;
`;

const GameImage = styled.img`
  width: 200px;
  height: 200px;
  border-radius: 100%;
  object-fit: cover;
  border: 5px solid ${props => props.theme.palette.primary};
  mix-blend-mode: ${props => props.blendMode || "unset"};
  object-position: ${props => props.position || "initial"};
`;

const PlayButton = styled(Link)`
  display: block;
  padding: 20px;
  border-radius: 10px;
  font-size: 2em;
  align-self: stretch;
  font-weight: bold;
  background-color: ${props => props.theme.palette.primary};
  color: ${props => props.theme.palette.primaryText};
  text-decoration: none;
`;

export default function GameCard(props) {
  return (
    <Card mx="50px" width="300px" height="600px">
      <GameImage blendMode="multiply" src={props.img}/>
      <h1>{props.name}</h1>
      <Box flex={1}>
        <h3 dangerouslySetInnerHTML={{
          __html:
          props.desc
        }}/>
      </Box>
      <PlayButton to={props.path}>Play!</PlayButton>
    </Card>
  );
}
