import React from "react";

import slotMachineSprites from "../../../../../assets/img/slot-sprites.png";
import "../../../../../assets/slot-machine.css";

const SlotMachineColumn = React.forwardRef( (props, ref) => {

  const INITIAL_TRANSITION_STYLE = {
    transition: "all 1s ease-in-out 0s",
    transform: "translateY(var(--spriteOffset0))"
  };

  const handleAnimationStart = (event) => {
    if (event.animationName === "blink-aura") { return }
    props.updateAnimationStart(props.index);
  };

  const handleAnimationEnd = () => {
    props.updateAnimationEnd(props.index);
  };

  return (
    <div className="col">
      <div ref={ref} style={INITIAL_TRANSITION_STYLE} onAnimationStart={handleAnimationStart} onAnimationEnd={handleAnimationEnd}>
        <img src={slotMachineSprites} className="sprite sprite-77" alt="three bar"/>
        <img src={slotMachineSprites} className="sprite sprite-0" alt="bell"/>
        <img src={slotMachineSprites} className="sprite sprite-1" alt="cherry"/>
        <img src={slotMachineSprites} className="sprite sprite-2" alt="grape"/>
        <img src={slotMachineSprites} className="sprite sprite-3" alt="seven"/>
        <img src={slotMachineSprites} className="sprite sprite-4" alt="gold"/>
        <img src={slotMachineSprites} className="sprite sprite-5" alt="bar"/>
        <img src={slotMachineSprites} className="sprite sprite-6" alt="two bar"/>
        <img src={slotMachineSprites} className="sprite sprite-7" alt="three bar"/>
        <img src={slotMachineSprites} className="sprite sprite-00" alt="bell"/>
      </div>
    </div>
    );
  });

export default SlotMachineColumn;