import React, { createContext, useContext, useState } from "react";
import axios from "axios";
import config from "../utilities/config";

const USER_LS_KEY = config.env.userKeyPath;
const HOST = config.env.HOST;
const API_BILLS_PATH = config.env.API.billsPath;
const API_PROFILE_PATH = config.env.API.profilePath;

let makeRequest = (url, options) => axios(url, options);

function getUserFromLS() {
  const user = localStorage.getItem(USER_LS_KEY);
  return user && JSON.parse(user);
}

function setUserToLS(user) {
  localStorage.setItem(USER_LS_KEY, JSON.stringify(user));
}

function removeUserFromLS() {
  localStorage.removeItem(USER_LS_KEY);
}

function getUser({ username, password }) {
  return makeRequest(HOST + API_PROFILE_PATH, { params: { username, password } });
}

function billUser(delta, description, url) {
  const { token } = getUserFromLS();
  return axios({
    url: HOST + API_BILLS_PATH,
    method: "POST",
    data: { delta, description, url },
    headers: {
      'x-token': token
    }
  });
}

const UserContext = createContext();

export const withUserData = Component => props => (
  <UserContext.Consumer>
    {data => <Component {...data} {...props} />}
  </UserContext.Consumer>
);

export const useUser = () => useContext(UserContext);

export const useCleanUser = () => {
  const { user } = useContext(UserContext);
  if (!user) {
    return null;
  }
  const {password, token, ...cleanUser} =  user;
  return cleanUser; 
};

export function UserProvider({ children }) {
  const [user, setUserState] = useState(getUserFromLS());

  function setUser(user) {
    if (!user) return;
    setUserToLS(user);
    setUserState(user);
  }

  function logout() {
    removeUserFromLS();
    setUserState(null);
  }

  const fetchUser = async ({ username, password }) => {
    const { data } = await getUser({ username, password });
    setUser(data);
  };

  const bill = async (delta, description, url) => {
    const { data } = await billUser(delta, description, url);
    setUser(data);
  };
  const ctx = { user, fetchUser, bill, logout };
  return <UserContext.Provider value={ctx}>{children}</UserContext.Provider>;
}
