import React from "react";

import { Redirect } from "@reach/router";
import { useCleanUser } from "../components/UserData";

import config from "./config";

const PrivateRoute = ({ as: Component }) => {
  const { loginPath } = config.env.APP_PATHS;
  const PrivatizedComponent = props => {
    const user = useCleanUser();
    return user ? <Component {...props} /> : <Redirect to={loginPath} noThrow />;
  };

  return <PrivatizedComponent />;
};

export default PrivateRoute;