import React, { useState, useRef, createRef } from "react";
import { Flex } from "reflexbox/styled-components";

import { getShuffleResultsForSlotSized } from "../../../../../utilities/utils";
import { useUser } from "../../../../UserData";

import "../../../../../assets/slot-machine.css";
import SlotMachineColumn from "./SlotMachineColumn";


export default function SlotMachine(game) {
  const [ errorMSG, setErrorMSG ] = useState('');
  const isErrorToDisplay = errorMSG && errorMSG.length;

  const slotColumnRefsInit = Array( game.nSlots ).fill(0).map(() => createRef());
  const slotColumnRefs = useRef( slotColumnRefsInit );

  const allTransitionsFinishedArr = Array( game.nSlots ).fill(true);
  const useTransitionIsFinishedArrRef = useRef(allTransitionsFinishedArr);

  const root = useRef();
  const results = useRef();

  const { bill } = useUser();

  const removeAllWinClassInstances = () => {
    // remove all .win class instances from sprites
    for (let el of root.current.querySelectorAll(".sprite")) { // eslint-disable-line no-unused-vars
      el.classList.remove("win");
    }
  };

  const showErrAndResetGameInternalState = (msg) => {
    setErrorMSG(msg);
    useTransitionIsFinishedArrRef.current = allTransitionsFinishedArr;
  };

  const chargeCustomerForGame = async () => {
    try {
      await bill(game.cost, game.billName, game.path);
    } catch(e) {
      showErrAndResetGameInternalState( "Problem Billing Your Account!" );
      return
    }
  };

  const getResultsForSlotGame = async () => {
    try {
      results.current = await getShuffleResultsForSlotSized(game.nSlots);
    } catch(e) {
      showErrAndResetGameInternalState( "Problem Getting Results!" );
      return
    }
  };

  const animateSlots = () => {
    // transitioning by moving to start->end->start->needed index,
    // if on start then end->start->end->needed index
    slotColumnRefs.current.forEach(
      (slotCol, index) => {
        slotCol.current.style.cssText = '';
        const  __SECRET_DO_NOT_DELETE_OR_YOU_WILL_BE_FIRED_USED_FOR_REANIMATING_ON_SAME_INDEX = slotCol.current.offsetWidth; // eslint-disable-line no-unused-vars

        const randomAnimationDuration = 2 + Math.random();
        const targetSequence = `animation: transition-to-${results.current.results[index]} ${randomAnimationDuration}s forwards`;
        slotCol.current.style.cssText = targetSequence;
      }
    );
  };

  const markSpritesWin = (slotValues) => {
    slotColumnRefs.current.map(
      slotCol => slotCol.current.querySelector(`.sprite-${slotValues[1]}`).classList.add("win")
    );
  };

  const checkTriplet = (slotValues, slotIndex) => {
    const isUpperDiag = slotValues[0] + 1 === slotValues[1] && slotValues[1] + 1 === slotValues[2];
    const isLowerDiag = slotValues[0] === slotValues[1] + 1 && slotValues[1] === slotValues[2] + 1;
    if (isUpperDiag || isLowerDiag) {
      slotColumnRefs.current[slotIndex].current.querySelector(`.sprite-${slotValues[1]}`).classList.add("win");
      slotColumnRefs.current[slotIndex + 1].current.querySelector(`.sprite-${slotValues[1]}`).classList.add("win");
      slotColumnRefs.current[slotIndex + 2].current.querySelector(`.sprite-${slotValues[1]}`).classList.add("win");
      return true;
    }
    return;
  };

  const checkIfWonAndPayout = async () => {

    const winResponseFromAPI = results.current.winningResults;

    if (winResponseFromAPI) {
      const slotValues = results.current.results;
      const isRowWin = slotValues.every( v => v === slotValues[0] );

      const isDiagWinButLessThan4Slots = slotValues.length<=3 && !isRowWin;
      if (isDiagWinButLessThan4Slots) {
        return
      }

      if (isRowWin) {// for n slots
        markSpritesWin(slotValues);
      } else {// [0,1,2],[1,2,3],[2,3,4],[3,4,5]... for n slots
        let i = 0;
        let foundDiag = false;
        while (i < slotValues.length - 2 && !foundDiag ) {
          const slotValuesSubArrayToLook = slotValues.slice(i, i + 3);
          foundDiag = checkTriplet(slotValuesSubArrayToLook, i);
          i++;
        }
      }

      await bill(
        -1 * game.prize,
        "Winning at " + game.name + " !",
        game.path
      );
    }
  };

  const onAnimationStartInCol = (index) => useTransitionIsFinishedArrRef.current[index] = false;

  const onAnimationEndInCol   = (index) => useTransitionIsFinishedArrRef.current[index] = true;

  const handleShuffleClick = async () => {
    if ( useTransitionIsFinishedArrRef.current.includes(false) ) {
      return
    }

    removeAllWinClassInstances();

    await chargeCustomerForGame();
    await getResultsForSlotGame();

    animateSlots();

    await checkIfWonAndPayout();

    if (isErrorToDisplay) {
      setErrorMSG('');
    }
  };

  return (
    <Flex
      height="100%"
      width="100%"
      justifyContent="center"
      alignItems="center"
      flexWrap="wrap"
    >
      <div ref={root} className="slot-machine-root">
        {slotColumnRefs.current.map( (ref, index) => (
          <SlotMachineColumn
            key={index}
            index={index}
            ref={ref}
            updateAnimationStart={onAnimationStartInCol}
            updateAnimationEnd={onAnimationEndInCol}
          />
        ))}

        <button
          className="slot-machine-toggle"
          onClick={handleShuffleClick}
        >
          Roll!
        </button>
      </div>

      <div style={{display: isErrorToDisplay ? 'block' : 'none', flexBasis: isErrorToDisplay ? '100%' : 'auto' }} ></div>
      <p style={{display: isErrorToDisplay ? 'block' : 'none' }} >{errorMSG}</p>

    </Flex>
  );
}

