import React from "react";

import { Link } from '@reach/router';

import { Box, Flex } from "reflexbox/styled-components";
import styled from "styled-components";


const GameImage = styled.img`
  width: 200px;
  min-width: 200px;
  height: 200px;
  min-height: 200px;
  border-radius: 100%;
  object-fit: cover;
  border: 5px solid ${props => props.theme.palette.primary};
  mix-blend-mode: ${props => props.blendMode || "unset"};
  object-position: ${props => props.position || "initial"};
`;

const Card = styled(Flex).attrs({
  px: "20px",
  py: "20px",
  alignItems: "center"
})`
  background-color: ${props => props.theme.palette.inverse};
  color: ${props => props.theme.palette.inverseText};
  box-shadow: ${props => props.theme.dimension.boxShadow + ' ' + props.theme.palette.boxShadow};
`;

const StyledLink = styled(Link)`
  display: block;
  padding: 50px;
  border-radius: 10px;
  font-size: 6em;
  font-weight: bold;
  background-color: ${props => props.theme.palette.primary};
  color: ${props => props.theme.palette.primaryText};
  text-decoration: none;

  @media screen and (max-width: 1400px) {
    transform: scale(0.7);
  }
`;

export default function SlotMachineSEO(props) {
  return (
    <Flex flexDirection="column" alignItems="center">
      <Card width="100%">
        <GameImage
          blendMode="multiply"
          position="center 20px"
          src={props.img}
        />
        <Flex flexDirection="column" height="100%" ml="10px">
          <h3>{props.name}</h3>
          <p>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec eget
            sem vestibulum, porta neque vel, blandit ex. Quisque tempus ornare
            metus, eget euismod odio efficitur vel. Nunc sagittis ex non tellus
            ornare lacinia. Morbi et rhoncus tortor, vel ultrices metus.
            Maecenas eu porta ligula. Ut porta est urna, id interdum nunc
            vestibulum in. Nulla nec tincidunt nisl. Aliquam in accumsan leo.
            Cras eget justo ac nisi sodales fringilla. Mauris nulla enim,
            lacinia eget nibh in, posuere malesuada ante. Praesent gravida nibh
            euismod mauris sagittis, at mattis quam convallis.
          </p>
          <p>
            Nulla tincidunt, magna eu luctus viverra, orci risus mattis est, ut
            dignissim sem lectus sit amet velit. Integer facilisis dui non
            fringilla cursus. Suspendisse potenti. Cras ex magna, porttitor ac
            lobortis ut, rutrum sed lorem. Pellentesque vitae dui enim. Donec
            feugiat ligula et magna congue commodo. Vestibulum sit amet ante
            quis eros convallis scelerisque vel consectetur arcu. Etiam pharetra
            at tortor elementum dignissim. Duis eu ultrices nisl. Nulla
            tincidunt, est a dapibus maximus, lectus metus blandit dolor, nec
            maximus massa nisi vel tellus. Praesent euismod est at urna pretium,
            vitae laoreet erat aliquam. Morbi quis accumsan urna, placerat
            lacinia nunc. Vivamus tempus rhoncus enim, sit amet malesuada metus
            molestie a.
          </p>
        </Flex>
      </Card>
      <Box mt="50px">
        <StyledLink to="/login">Play now!</StyledLink>
      </Box>
    </Flex>
  );
}
