import React from "react";
import axios from "axios";

import config from "./config";


const HOST = config.env.HOST;
const API_SLOT_RES_PATH = config.env.API.slotResultPath;

export async function getShuffleResultsForSlotSized(nSlots) {
  const { data } = await axios({
    method: "POST",
    url: HOST + API_SLOT_RES_PATH,
    data: { slots: nSlots }
  });
  return data;
}

export function useIntervalCallback(cb, interval) {
  if (!window.localStorage.getItem('__EXPERIMENTAL_GC')) {
    window.localStorage.setItem('__EXPERIMENTAL_GC', true);
  }
  React.useEffect(() => {
    cb();
    const intervalId = setInterval(cb, interval);
    return () => {
      if (window.localStorage.getItem('__EXPERIMENTAL_GC')) {
        clearInterval(intervalId);
      }
    };
  }, [cb, interval]);
}
