import React, { useRef } from "react";

import { Redirect } from '@reach/router';

import styled from "styled-components";
import { Box, Flex } from "reflexbox/styled-components";

import config from "../../../utilities/config";
import { useUser } from "../../UserData";

const LoginBox = styled(Box).attrs({ width: "500px" })`
  background-color: ${props => props.theme.palette.secondary};
  box-shadow: 1px 1px 5px black;
`;

const LoginButton = styled.button`
  padding: 20px;
  font-size: 2em;
  align-self: stretch;
  font-weight: bold;
  background-color: ${props => props.theme.palette.primary};
  color: ${props => props.theme.palette.primaryText};
  border: none;
  &:focus {
    outline: none;
  }
  &:disabled {
    opacity: 0.3;
  }
`;

const LoginForm = styled.form`
  display: flex;
  flex-direction: column;
`;

const Label = styled.label`
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 1.5em;
  color: ${props => props.theme.palette.secondaryText};
`;

const Error = styled.p`
  text-align: center;
  color: ${props => props.theme.palette.negative};
`;


export default function Login() {
  const { gamesPath } = config.env.APP_PATHS;

  const useHasError = useRef(false);

  const useUsername = useRef();
  const usePassword = useRef();
  const useSubmitButton = useRef();

  const { user, fetchUser } = useUser();


  const isUsernameEmptyOrPasswordEmpty = () => (useUsername.current.value.length === 0 || usePassword.current.value.length === 0);


  const checkForm = () => {
    useSubmitButton.current.disabled = isUsernameEmptyOrPasswordEmpty();
  };

  async function submit(e) {
    e.preventDefault();
    if ( !isUsernameEmptyOrPasswordEmpty() ) {
      try {
        useHasError.current = false;
        await fetchUser({ password: usePassword.current.value, username: useUsername.current.value });
      } catch (e) {
        useHasError.current = true;
      }
    }
  }

  if (user) {
    return (<Redirect to={gamesPath} noThrow/>);
  }

  return (
    <Flex
      height="100%"
      width="100%"
      alignItems="center"
      justifyContent="center"
    >
      <LoginBox>
        <LoginForm onSubmit={submit}>
          <Box flex={1} px="50px" pt="50px">
            <Label>
              Username
              <input
                type="text"
                name="username"
                autoComplete="username"
                placeholder="awesome-possome"
                ref={useUsername}
                onChange={checkForm}
              />
            </Label>
          </Box>
          <Box px="50px" py="30px" flex={1}>
            <Label>
              Password
              <input
                type="password"
                name="password"
                autoComplete="current-password"
                placeholder="123456"
                ref={usePassword}
                onChange={checkForm}
              />
            </Label>
          </Box>
          {useHasError.current && <Error>Bad username or password</Error>}
          <LoginButton ref={useSubmitButton} disabled>Login</LoginButton>
        </LoginForm>
      </LoginBox>
    </Flex>
  );
}
