import React from "react";
import { Router } from "@reach/router";

import PrivateRoute from "../../../utilities/PrivateRoute";

import PageNotFound from "./PageNotFound";
import WelcomePage from "./WelcomePage";
import Login from "./Login";

import All from "./All";
import Games from "./Games";

import UserPage from "./UserPage/UserPage";

import config from '../../../utilities/config';
import OnRouteChange from "../../../utilities/RouteTransitionExtension";

export default function Main() {
  const { loginPath, profilePath, gamesPath } = config.env.APP_PATHS;
  return (
    <>
      <Router>
        <PageNotFound default />
        <WelcomePage path="/"/>
        <Login path={loginPath}/>
        <PrivateRoute path={gamesPath} as={All}/>
        <Games path={gamesPath+"/*"} />
        <PrivateRoute path={profilePath} as={UserPage}/>
      </Router>
      <OnRouteChange actions={ ["scrollToTop", "updatePageViewGA"] } />
    </>
  );
}
