import React from "react";
import styled from "styled-components";
import Gravatar from "react-gravatar";
import { Box, Flex } from "reflexbox/styled-components";
import { Link } from '@reach/router';
import { useCleanUser } from "../../UserData";

import TopBar from "../elements/Topbar";

const Logo = styled(Link)`
  font-size: 2em;
  color: ${props => props.theme.palette.primaryText};
  text-decoration: none;
`;

const TopBarAccountLink = styled(Link)`
  color: ${props => props.theme.palette.primaryText};
  text-decoration: none;
`;

const TopBarAccount = () => {
  const { username, email } = useCleanUser();

  return (
    <Flex flex={1} justifyContent="flex-end">
      <TopBarAccountLink to="/me">
        <Flex alignItems="center">
          <Box mr="10px">
            <span>{username}</span>
          </Box>
          <Gravatar size={30} email={email}/>
        </Flex>
      </TopBarAccountLink>
    </Flex>
  );
};

const TopBarContainer = () => {
  const user = useCleanUser();
  const logoPath = !!user ? "/games" : "/";
  return (
    <TopBar>
      <Logo to={ logoPath }><span role="img" aria-label="Four Leaf Clover Emoji">üçÄ</span> CLOVER</Logo>
      { !!user ? <TopBarAccount/> : null }
    </TopBar>
  );

};

export default TopBarContainer;
