import React from "react";
import { Box, Flex } from "reflexbox/styled-components";
import { Router } from '@reach/router';

import PageNotFound from "../PageNotFound";
import GamesSidebar from "./GamesSidebar";

import SlotMachine from "./SlotMachine/SlotMachine";
import SlotMachineSEO from "./SlotMachine/SlotMachineSEO";

import { useCleanUser } from "../../../UserData";
import { games } from "../../../../utilities/config";

const gameComponents = {
  SlotMachine,
  SlotMachineSEO
};

const GetGameForAuthElseSEO = ({ as, seo, game }) => {

  const PrivatizedComponent = () => {
    
    const user = useCleanUser();
    const GameComponent = gameComponents[as];
    const SEOComponent = gameComponents[seo];

    return user ? (<GameComponent {...game} />) : (<SEOComponent {...game} />);
  };

  return <PrivatizedComponent />;
};

export default function Games() {
  return (
    <Flex
      justifyContent="center"
      alignItems="center"
      height="100%"
      width="100%"
    >
      <GamesSidebar/>
      <Box flex={1}>
        <Router>
          <PageNotFound default />
          { games.map( (game) => (
              <GetGameForAuthElseSEO
                key={game.id}
                as={game.JSX_ID}
                seo={game.JSX_SEO_ID}
                path={game.path}
                game={game}
              />
              )
            )
          }
        </Router>
      </Box>
    </Flex>
  );
}