import React from "react";
import { Flex } from "reflexbox/styled-components";

import GameCard from "./GameCard";

import { games } from "../../../utilities/config";

export default function All() {
  return (
    <Flex
      justifyContent="center"
      alignItems="center"
      height="100%"
      width="100%"
    >
      { games.map( (game) => (
          <GameCard
            key={game.id}
            name={game.name}
            desc={game.desc}
            img={game.img}
            path={game.path}
          />
          )
        )
      }
    </Flex>
  );
}
