import React, { useCallback, useState } from "react";

import { Flex } from "reflexbox/styled-components";
import styled, { css } from "styled-components";

import axios from "axios";

import moment from "moment";
import { Link } from '@reach/router';

import config from "../../../../utilities/config";

import { useIntervalCallback } from "../../../../utilities/utils";
import { useUser } from "../../../UserData";

import Loading from "./Loading";

const HOST = config.env.HOST;
const API_BILLS_PATH = config.env.API.billsPath;

const Table = styled.table`
  width: 100%;
  border-collapse: collapse;
  th {
    position: sticky;
    top: 0;
    background-color: ${props => props.theme.palette.secondary};
    padding-top: 10px;
  }
  tfoot {
    td {
      position: sticky;
      bottom: 0;
      background-color: ${props => props.theme.palette.secondary};
    }
  }
  tbody {
    tr {
      height: 30px;
      &:nth-child(even) {
        background-color: rgba(255, 255, 255, 0.1);
      }

      td {
        border-spacing: 0;
      }
    }
  }
`;

const BillLink = styled(Link)`
  text-decoration: none;
  color: ${props => props.theme.palette.primary};
`;

let [positive, negative] = ["positive", "negative"];
const balanceColor = css`
  color: ${props =>
  props.theme.palette[parseInt(props.children) > 0 ? positive : negative]};
`;

const BalanceText = styled.span`
  ${balanceColor}
`;

const TimeAgo = styled.span`
  text-indent: 10px;
  display: inline-block;
  opacity: 0.4;
`;

export default function BillingHistory() {
  const [billing, setBilling] = useState(null);
  const { user: { token } } = useUser();
  const fetchBilling = useCallback(
    () => axios(HOST + API_BILLS_PATH, {
      headers: {
        'x-token': token
      }
    }).then(({ data }) => setBilling(data.reverse())),
    []
  );
  useIntervalCallback(fetchBilling, 10000);
  if (!billing) {
    return (
      <Flex
        flex={1}
        alignItems="center"
        justifyContent="center"
        height="100%"
        width="100%"
      >
        <Loading/>
      </Flex>
    );
  }
  return (
    <Flex flex={1} height="100%" width="100%" style={{ overflowY: "scroll" }}>
      <Table role="grid">
          <caption>Transaction Summary</caption>
          <tbody>
            <tr>
              <th scope="col">Description</th>
              <th scope="col">Amount</th>
              <th scope="col">Balance</th>
              <th scope="col">Time of billing</th>           
            </tr>

            {billing.map((bill, idx) => (
              <tr key={idx}>
                <td tabIndex="0">
                  {bill.url ? (
                    <BillLink to={bill.url} tabIndex="0">{bill.description}</BillLink>
                  ) : (
                    bill.description
                  )}
                </td>
                <td tabIndex="0">
                  <BalanceText>{-bill.delta}</BalanceText>
                </td>
                <td tabIndex="0">
                  <BalanceText>{bill.balance}</BalanceText>
                </td>
                <td tabIndex="0">
                  {moment(bill.timestamp).format("dddd, MMMM Do YYYY, h:mm:ss a")}
                  <TimeAgo>({moment(bill.timestamp).fromNow()})</TimeAgo>
                </td>
              </tr>
            ))}
        </tbody>
        <tfoot>
          <tr>
            <td/>
            <td colSpan={3}>
              <Flex
                width="100%"
                alignItems="center"
                height="20px"
                px="25px"
                py="5px"
              >
                <BalanceText>
                  {billing.reduce((sum, { delta }) => sum - delta, 0)}
                </BalanceText>
                &nbsp;&nbsp;Total
              </Flex>
            </td>
          </tr>
        </tfoot>
      </Table>
    </Flex>
  );
}