import React from "react";
import styled from "styled-components";
import { Link } from '@reach/router';
import { Box, Flex } from "reflexbox/styled-components";

import casino from "../../../assets/img/casino.jpeg";
import slotMachine from "../../../assets/img/slot-machine.jpg";
import bigSlotMachine from "../../../assets/img/big-slot-machine.jpg";

const Container = styled(Flex).attrs({
  width: "100%",
  justifyContent: "center",
  alignItems: "center",
  flexDirection: "column"
})`
  background-image: url(${casino});
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  height: -moz-available;
  height: webkit-fill-available;
  height: fill-available;
`;

const MegaTitle = styled.h1`
  font-size: 10vw;
  text-shadow: 1px 1px 20px black;
  margin-bottom: 0;
`;

const Title = styled.h1`
  font-size: 4vw;
  text-shadow: 1px 1px 10px black;

  a {
    text-decoration: none;
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 5px;
    display: block;

    color: ${props => props.theme.palette.primary};
  }
`;

const StyledLink = styled(Link)`
  display: block;
  padding: 5vh 5vw;
  border-radius: 10px;
  font-size: 5vw;
  font-weight: bold;
  background-color: ${props => props.theme.palette.primary};
  color: ${props => props.theme.palette.primaryText};
  text-decoration: none;
  background-color: ${props => props.theme.palette.primary};

  @media screen and (max-width: 1400px) {
    transform: scale(0.7);
  }
`;

const GameImage = styled.img`
  width: 200px;
  min-width: 200px;
  height: 200px;
  min-height: 200px;
  border-radius: 100%;
  object-fit: cover;
  border: 5px solid ${props => props.theme.palette.primary};
  mix-blend-mode: ${props => props.blendMode || "unset"};
  object-position: ${props => props.position || "initial"};
`;

const Card = styled(Flex).attrs({
  px: "20px",
  py: "20px",
  alignItems: "center"
})`
  background-color: ${props => props.theme.palette.inverse};
  color: ${props => props.theme.palette.inverseText};
  box-shadow: ${props => props.theme.dimension.boxShadow + ' ' + props.theme.palette.boxShadow};
`;

