import React from "react";

import { Match, Link } from '@reach/router';

import styled from "styled-components";
import { Box, Flex } from "reflexbox/styled-components";

import { useCleanUser } from "../../../UserData";
import { games } from "../../../../utilities/config"

const Sidebar = styled(Flex).attrs({
  as: "aside",
  width: "200px",
  height: "100%",
  p: "10px",
  flexDirection: "column"
})`
  background-color: ${props => props.theme.palette.secondary};
  color: ${props => props.theme.palette.secondaryText};

  hr {
    background-color: currentColor;
    border: none;
    width: 100%;
    height: 1px;
  }
`;

const BalanceText = styled.h3`
  margin-top: 0;
  margin-bottom: 0.5em;
  color: ${props =>
  props.theme.palette[
    parseInt(props.children) > 0 ? "positive" : "negative"
    ]};
`;

const GameImage = styled.img`
  width: 100px;
  height: 100px;
  border-radius: 100%;
  object-fit: cover;
  border: 5px solid ${props => props.theme.palette.primary};
  mix-blend-mode: ${props => props.blendMode || "unset"};
  object-position: ${props => props.position || "initial"};
`;

function GameIconLink({ game }) {
  return (
    <Match path={game.path}>
      {props =>
        props.match ? (
          null
        ) : (
          <Box my="10px" alignSelf="center">
            <Link to={game.path}>
              <GameImage src={game.img}/>
            </Link>
          </Box>    
        )
      }
    </Match>
  );
}

export default function GamesSidebar() {

  const user = useCleanUser();

  if (!user) return null;
  return (
    <Sidebar>
      <h2>Balance</h2>
      <BalanceText>{user.balance}$</BalanceText>
      <hr/>
      { games.map( (game) => (
            <GameIconLink
              key={game.id}
              game={game}
            />            
          )
        )
      }
    </Sidebar>
  );
}
